import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    type: 1,
    districtCode: "",
    iframeUrl: "",
  },
  getters: {
    getType(state) {
      return state.type;
    },
    getDistrictCode(state) {
      return state.districtCode;
    },
  },
  mutations: {
    SET_TYPE: (state, data) => {
      state.type = data;
    },
    SET_DISTRICT_CODE: (state, data) => {
      state.districtCode = data;
    },
    SET_IFRAME_URL: (state, data) => {
      state.iframeUrl = data;
    },
  },
  actions: {
    setType: ({ commit }, data) => {
      commit("SET_TYPE", data);
    },
    setDistrictCode: ({ commit }, data) => {
      commit("SET_DISTRICT_CODE", data);
    },
    setIframeUrl: ({ commit }, data) => {
      commit("SET_IFRAME_URL", data);
    },
  },
  modules: {},
});
