// 大屏自适应适配
export const adaptive = () => {
  // * 默认缩放值
  const scale = {
    width: "1",
    height: "1",
  };
  // * 设计稿尺寸（px）
  const baseWidth = 1920;
  const baseHeight = 1080;

  let timer = null;

  // * 需保持的比例（默认1.77778）
  const baseProportion = parseFloat(baseWidth / baseHeight);

  const dom = document.getElementById("app-container");

  const calcRate = () => {
    // 当前宽高比
    const currentRate = parseFloat(window.innerWidth / window.innerHeight);
    if (currentRate > baseProportion) {
      // 表示更宽
      scale.width = (window.innerHeight * baseProportion) / baseWidth;
      scale.height = window.innerHeight / baseHeight;
      dom.style.transform =
        "scale(" +
        scale.width +
        ", " +
        scale.height +
        ") translate(-50%, -50%)";
    } else {
      // 表示更高
      scale.height = window.innerWidth / baseProportion / baseHeight;
      scale.width = window.innerWidth / baseWidth;
      dom.style.transform =
        "scale(" +
        scale.width +
        ", " +
        scale.height +
        ") translate(-50%, -50%)";
    }
  };

  const resize = function resize() {
    clearTimeout(timer);
    timer = setTimeout(function () {
      calcRate();
    }, 200);
  };

  // 改变窗口大小重新绘制
  const windowDraw = function windowDraw() {
    window.addEventListener("resize", resize);
  };

  windowDraw();
  calcRate();
};

const add_zero = (temp) => {
  if (temp < 10) return "0" + temp;
  else return temp;
};

// 获取当前时间
export const getTime = () => {
  const myDate = new Date();

  const year = myDate.getFullYear(); //年

  const month = myDate.getMonth() + 1; //月

  const day = myDate.getDate(); //日

  const hours = add_zero(myDate.getHours());

  const minutes = add_zero(myDate.getMinutes());

  return year + "-" + month + "-" + day + " " + hours + ":" + minutes;
};
